import Exp from './exp'
import observer from './factory'

class ExpressionFactory {
  constructor() {
    this.exps = new Map();
  }

  _bind(exp) {
    let obj = observer.obj(exp.target),
      map = this.exps.get(obj);

    if (!map) {
      map = {};
      this.exps.set(obj, map)
    }
    map[exp.expression] = exp;
  }

  _unbind(exp) {
    let obj = observer.obj(exp.target),
      map = this.exps.get(obj);

    if (map && map[exp.expression] === exp) {
      delete map[exp.expression];

      for (let key in map) {
        return;
      }
      this.exps.delete(obj);
    }
  }

  _get(obj, exp) {
    let map;

    obj = observer.obj(exp.target);
    map = this.exps.get(obj);
    if (map)
      return map[exp];
    return undefined;
  }

  _on(obj, exp, handler) {
    let path = toPath(exp);

    if (path.length > 1) {
      let exp = this._get(obj, exp);

      if (!exp) {
        exp = new Exp(obj, exp, path);
        this._bind(exp);
      }
      exp.addListen(handler);
      return exp.target;
    } else {
      return observer.on(obj, exp, handler);
    }
  }

  _un(obj, exp, handler) {
    let path = toPath(exp);

    if (path.length > 1) {
      let exp = this._get(obj, exp);

      if (exp) {
        exp.removeListen(handler);
      }
      if (!exp.hashListen()) {
        this._unbind(exp);
        return exp.destory();
      }
      return exp.target;
    } else {
      return observer.un(obj, exp, handler);
    }
  }

  on(obj) {
    if (arguments.length < 2) {
      throw TypeError('Invalid Parameter');
    } else if (arguments.length === 2) {
      let p1 = arguments[1];
      if (typeof p1 === 'function') {
        return observer.on(obj, p1);
      } else if (p1 && typeof p1 === 'object') {
        for (let exp in p1) {
          if (typeof p1[exp] !== 'function') {
            throw TypeError('Invalid Observer Handler');
          }
          obj = this._on(obj, exp, p1[exp]);
        }
      } else {
        throw TypeError('Invalid Parameter');
      }
    } else if (arguments.length >= 3) {
      let i,
        exps = [],
        handler = undefined;

      for (i = 0; i < arguments.length; i++) {
        if (typeof arguments[i] === 'function') {
          handler = arguments[i];
          break;
        }
        if (arguments[i] instanceof Array) {
          exps.push.apply(exps, arguments[i]);
        } else {
          exps.push(arguments[i]);
        }
      }
      if (!handler) {
        throw TypeError("Invalid Observer Handler", handler);
      }
      for (i = 0; i < exps.length; i++) {
        obj = this._on(obj, exps[i] + '', handler);
      }
    }
    return obj;
  }


  un(obj) {
    if (arguments.length < 1) {
      throw TypeError('Invalid Parameter');
    } else if (arguments.length === 1) {
      return observer.un(obj);
    } else if (arguments.length === 2) {
      let p1 = arguments[1];

      if (p1 instanceof Array) {
        for (let i = 0; i < p1.length; i++) {
          obj = this._on(obj, p1);
        }
      } else if (p1 && typeof p1 === 'object') {
        for (let exp in p1) {
          obj = this._un(obj, exp, p1[exp]);
        }
      } else {
        obj = this._un(obj, p1 + '');
      }
    } else if (arguments.length >= 3) {
      let i;

      exps = [];
      handler = undefined;
      for (i = 0; i < arguments.length; i++) {
        if (typeof arguments[i] === 'function') {
          handler = arguments[i];
          break;
        }
        if (arguments[i] instanceof Array) {
          exps.push.apply(exps, arguments[i]);
        } else {
          exps.push(arguments[i]);
        }
      }
      for (i = 0; i < exps.length; i++) {
        obj = this._un(obj, exps[i] + '', handler);
      }
    }
    return obj;
  }
}

ExpressionFactory.prototype.obj = observer.obj;
ExpressionFactory.prototype.eq = observer.eq;

export default new ExpressionFactory();




